import * as XLSX from 'xlsx';
import { useState } from 'react';
import style from './App.module.css';

function Generador() {
    return (
        <div className='fs-6 p-3' >
            <MiComponente />
        </div>
    );
}

function MiComponente() {
    const [texto, setTexto] = useState('');
    const handleTextoChange = (event) => {
        setTexto(event.target.value);
    }
    const generarExcel = () => {
        if (verificar_datos(texto)) {
            // Crear el libro de Excel
            const workbook = XLSX.utils.book_new();
            // Crear una hoja de clculo
            const worksheet = XLSX.utils.aoa_to_sheet(obtener_lista(texto));
            // Aadir la hoja de clculo al libro
            XLSX.utils.book_append_sheet(workbook, worksheet, 'Productos');
            // Generar el archivo de Excel
            XLSX.writeFile(workbook, 'lista_de_prodcutos.xlsx');
        }
    }
    return (
        <div>
            <div className='fs-1'>Datos del Lote</div>
            <textarea
                className={style.cuadro_texto}
                placeholder="Ingresa tu texto aqu"
                rows="20"
                wrap="off"
                value={texto}
                onChange={handleTextoChange}
            />
            <button style={{ width: "100%", margin: "1rem 0rem", height: "3rem", fontSize: "1rem", fontWeight: "bold", background: "greenyellow" }} onClick={generarExcel}>GENERAR EXCEL</button>
            <MostrarTabla texto={texto} />
        </div>
    );
}

function MostrarTabla(props) {
    const texto = props.texto
    let mensaje = 'no hay nada'
    const aux = []
    if (verificar_datos(texto)) {
        const vector = obtener_lista(texto)
        aux.push(
            <thead className='text-center'>
                <tr key={vector[0][0]}>
                    <th>{vector[0][0]}</th>
                    <th>{vector[0][1]}</th>
                    <th>{vector[0][2]}</th>
                    <th>{vector[0][3]}</th>
                    <th>{vector[0][9]}</th>
                </tr>
            </thead>
        )
        for (let i = 1; i < vector.length; i++) {
            aux.push(
                <tbody className='lh-1'>
                    <tr key={parseInt(vector[i][0])} >
                        <th className='text-center'>{vector[i][0]}</th>
                        <th>{vector[i][1]}</th>
                        <th>{vector[i][2]}</th>
                        <th className='text-center'>{vector[i][3]}</th>
                        <th className='text-center'>{vector[i][9]}</th>
                    </tr>
                </tbody>
            )
        }
        aux.push(
            <tr>
                <th></th><th></th><th></th>
                <th className='text-center'>{sumar_columna(vector,3)}</th>
                <th className='text-center'>{sumar_columna(vector,9)}</th>
            </tr>
        )
    }
    mensaje = aux
    return (
        <table className="table table-bordered" style={{ width: "100%" }}>
            {mensaje}
        </table>
    );
}

function verificar_datos(texto) {
    if (texto.search('\n') != -1) {
        const vector = texto.split('\n')
        for (let i = 0; i < vector.length; i++) {
            if (!verificar_atributos(vector[i])) {
                return false;
            }
        }
        return true;
    } else {
        return false;
    }
}

function verificar_atributos(texto) {
    let contador = 0
    const atributos = ['Descripcin', 'Caractersticas', 'Cantidad', 'Unidad de Medida', 'Industria', 'Fecha Vencimiento', 'Modelo', 'Estado', 'Peso Kg.', 'Observaciones']
    for (let i = 0; i < atributos.length; i++) {
        if (texto.search(atributos[i]) != -1) {
            contador = contador + 1
        }
    }
    if (contador == atributos.length) {
        return true;
    } else {
        return false;
    }
}

function obtener_lista(texto) {
    const lista = texto.split('\n')
    const lista_completa = [
        ['NRO', 'DESCRIPCIN', 'CARACTERSTICAS', 'CANTIDAD', 'UNIDAD DE MEDIDA', 'INDUSTRIA', 'FECHA VENCIMIENTO', 'MODELO', 'ESTADO', 'PESOKG.', 'OBSERVACIONES']
    ]
    for (let i = 0; i < lista.length; i++) {
        lista_completa.push(separar_atributos(lista[i]))
    }
    // convertir cantidad
    for (let i = 1; i < lista_completa.length; i++) {
        // corregir nro
        lista_completa[i][0] = parseInt(lista_completa[i][0]);
        // corregir cantidad
        if (buscar_punto(lista_completa[i][3]) == -1) {
            lista_completa[i][3] = parseInt(lista_completa[i][3])
        } else {
            lista_completa[i][3] = parseFloat(lista_completa[i][3])
        }
        //corregir peso
        if (buscar_punto(lista_completa[i][9]) == -1) {
            lista_completa[i][9] = parseInt(lista_completa[i][9])
        } else {
            lista_completa[i][9] = parseFloat(lista_completa[i][9])
        }
    }
    return lista_completa;
}

function separar_atributos(texto) {
    const detalles = []
    const atributos = ['Descripcin', 'Caractersticas', 'Cantidad', 'Unidad de Medida', 'Industria', 'Fecha Vencimiento', 'Modelo', 'Estado', 'Peso Kg.', 'Observaciones']

    detalles.push(texto.substring(0, texto.search('')))

    for (let i = 0; i < 9; i++) {
        const pos1 = texto.search(atributos[i]) + atributos[i].length
        const pos2 = texto.search(atributos[i + 1])
        detalles.push(texto.substring(pos1 + 2, pos2 - 1))
    }
    detalles.push(texto.substring(texto.search(atributos[9]) + atributos[9].length + 2, texto.length))
    return detalles;
}

function buscar_punto(texto) {
    let posicion = -1
    for (let i = 0; i < texto.length; i++) {
        if (texto.substring(i, i + 1) == '.') {
            posicion = i
        }
    }
    return posicion
}

function sumar_columna(vector, col){
    const vec = vector
    let c = 0
    for (let i = 1; i <vector.length; i++) {
        c = c + vector[i][col]
    }
    return c
}

export default Generador;